const https = require('https');
const http = require('http');

class ApiClient {
  constructor() {
    this.baseURL = 'random-data-api.com';
    this.timeout = 30000;
  }

  async makeRequest(path) {
    return new Promise((resolve, reject) => {
      const options = {
        hostname: this.baseURL,
        port: 443,
        path: path,
        method: 'GET',
        headers: {
          'User-Agent': 'User-Management-App/1.0',
          'Accept': 'application/json'
        },
        timeout: this.timeout
      };

      const req = https.request(options, (res) => {
        if (res.statusCode !== 200) {
          reject(new Error(`HTTP ${res.statusCode}`));
          return;
        }

        let data = '';
        res.on('data', (chunk) => {
          data += chunk;
        });

        res.on('end', () => {
          try {
            const parsed = JSON.parse(data);
            resolve(parsed);
          } catch (parseError) {
            reject(new Error('Invalid JSON response'));
          }
        });
      });

      req.on('error', (error) => {
        reject(new Error(`Network error: ${error.message}`));
      });

      req.on('timeout', () => {
        req.destroy();
        reject(new Error('Request timeout'));
      });

      req.end();
    });
  }

  async getUsers(size = 10) {
    try {
      console.log(`Fetching ${size} users from Random Data API...`);
      
      const data = await this.makeRequest(`/api/v2/users?size=${size}&response_type=json`);
      
      console.log('Successfully fetched users from API');
      
      // Ensure we always return an array
      const users = Array.isArray(data) ? data : [data];
      
      return users.map((user, index) => ({
        id: user.id || index + 1,
        uid: user.uid || `uid-${user.id || index}`,
        first_name: user.first_name || '',
        last_name: user.last_name || '',
        username: user.username || '',
        email: user.email || '',
        phone_number: user.phone_number || '',
        date_of_birth: user.date_of_birth || '',
        avatar: user.avatar,
        employment: user.employment,
        address: user.address
      }));
      
    } catch (error) {
      console.error('API request failed:', error.message);
      throw error; // Re-throw to handle in API route
    }
  }

  async getSingleUser() {
    try {
      const user = await this.makeRequest('/api/v2/users');
      return {
        id: user.id,
        uid: user.uid,
        first_name: user.first_name,
        last_name: user.last_name,
        username: user.username,
        email: user.email,
        phone_number: user.phone_number,
        date_of_birth: user.date_of_birth,
        avatar: user.avatar,
        employment: user.employment,
        address: user.address
      };
    } catch (error) {
      console.error('API request failed:', error.message);
      throw error;
    }
  }
}

module.exports = new ApiClient();